	module.exports = {"data":{"markdownRemark":{"html":"<p>Both Chrome and Firefox have excellent JavaScript consoles, also known as DevTools, for debugging your JavaScript.</p>\n<p>You can find Developer tools in your Chrome’s menu or Web Console in FireFox’s menu. If you’re using a different browser, or a mobile phone, we strongly recommend switching to desktop Firefox or Chrome.</p>\n<p>You can also use <a href='https://repl.it/' target='_blank' rel='nofollow'>https://repl.it/</a> to run code online.</p>\n<p>This is how you print to the console:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>console.log('Hello world!')</code></pre>\n      </div>","fields":{"slug":"/javascript/tutorials/use-the-javascript-console/"},"frontmatter":{"title":"Use the JavaScript Console"}}},"pathContext":{"slug":"/javascript/tutorials/use-the-javascript-console/"}}

